form {
	background-color: rgba(0, 0, 0, 0.887);
	display: flex;
	flex-direction: column;
	gap: 20px;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	justify-content: center;
	align-items: center;
	z-index: 9999;
}

.form-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 20px;
}

label {
	width: 100px;
}

input[type='text'] {
	width: 250px;
	background-color: black;
	border: 1px solid gray;
	padding: 6px;
	color: white;
}

input[type='text']:focus {
	outline: none;
	border: 1px solid white;
}
